	const login = require('../../static/utils/utils').Login;
	const query = require('../../static/utils/utils').Query;
	const Login = new login();
	const Query = new query();
	
	import {formatDate} from '../../static/utils/time.js'
	import {addFormId,compareTime} from '../../static/utils/utils.js';
	
	//引入api
	import {taskAdd,departmentQuery,taskQuery,messageSend,projectQuery,sprintQuery} from '../../static/utils/api.js';
	var _this;
		export default {
			data() {
				return {
					inputWidth:"",           //输入框的长度
					
					userInfo:"",             
                    projectId:"",           //项目的id
					departmentId:"",       //专业的id
					taskExcuteUserId:"",    //执行人的id默认为该用户的id
					taskChargeUserId:"",    //该项目的负责人的id,默认为该项目的权限1的用户的id
					sprintId:true,            //冲刺的id
					workNote:"",            //任务的备注信息
					
					taskChargeUserName:"",   //该项目的负责人的名字
					taskPriority:"",         //项目的优先级（1、2、3、4）;
					taskOrder:"",            //任务的序号从专业里面取跟序号进行拼接
				    taskPredictTime:"",      //任务的预估时间
					taskName:"",             //任务的内容
					
					departmentName:"",      //专业的名称
					openId:"",             //项目负责人的openId
					projectName:"",       //项目的名称
				
					
					
				    priority:[1,2,3,4],      //优先级实现可以改变
					index:""                 ,//选择优先级的下标
					
				    departmentNum:"",            //从专业中取到的任务的标识号
					
				}
			},
			onLoad(){
				 this.getSystem();
			},
			onShow(){
				_this = this;
				uni.getStorage({
					key:"userInfo",
					success:(res)=>{
						let id = {
							id:res.data.id
						}
						Query.findUser(id)
						.then(data=>{
							//console.log(data.data.records[0]);
							_this.userInfo = data.data.records[0];
							_this.taskExcuteUserId = _this.userInfo.id;
							//_this.taskChargeUserName = _this.userInfo.trueName;
	                        uni.getStorage({
	                        	key:"nowInProject",
								success:(res)=>{
					                _this.projectId = res.data.projectId;   //设置项目的id
									uni.getStorage({
										key:"sprintId",
										success:(res)=>{
											_this.sprintId = res.data;     //设置冲刺
											if(!_this.sprintId){  //没有冲刺  则显示暂无冲刺信息
												_this.sprintId = "";
											}else{
											  _this.getChargeUserId();
											  _this.getDepartmentId(); 
											  _this.findProjectNameByProjectId();	
											}
										},
										fail:()=>{
											_this.sprintId = "";
										}
									})
								},
								fail:()=>{
									uni.redirectTo({
										url:'../apply/apply'
									})
								}
	                        })
						}) 
					},
					fail:()=>{
						uni.redirectTo({
							url:"../login/login"
						})
					}
				})
			},
			methods: {
				
			  //获取系统的信息设置输入框的长度,根据设置的左边的文字占的文字去计算
			  getSystem:function(){
				  _this = this;
				  uni.getSystemInfo({
				  	success:(res)=>{
						_this.inputWidth = parseInt(res.windowWidth)-80;
					}
				  })
			  },	
				
			  //寻找该项目下的权限为1的用户即项目的负责人
			  getChargeUserId:function(){
				  _this = this;
				  console.log("项目",_this.projectId)
				  uni.showLoading({
				  	title:"获取中",
					success:()=>{
						Query.findUserProjectRoleByRoleAndProject(1,_this.projectId)
						.then(data=>{
							uni.hideLoading();
							console.log("查询到的项目负责人的关系",data.data.records[0]);
							_this.taskChargeUserId = data.data.records[0].userId;
							_this.getChargeUserTrueName();
						})
						.catch(Error=>{
							uni.showToast({
								title:"网络错误",
								duration:1000,
								icon:'loading'
							})
						})
					}
				  })
			  },
			  
			  //寻找该项目下项目负责人的真实姓名
			  getChargeUserTrueName:function(){
				_this = this;
				uni.showLoading({
				  	title:"获取中",
				  	success:()=>{
						let id = {
							id:_this.taskChargeUserId
						}
				  		Query.findUser(id)
				  		.then(data=>{
				  		    uni.hideLoading();
				  			//console.log(data)
				  			console.log("查询到的项目负责人的真实姓名",data.data.records[0].trueName);
				  			_this.taskChargeUserName = data.data.records[0].trueName;
							_this.openId = data.data.records[0].openId;
				  		})
				  		.catch(Error=>{
				  			uni.showToast({
				  				title:"网络错误",
				  				duration:1000,
				  				icon:'loading'
				  			})
				  		})
				    }
				})
			 },
			 
			 //获取自己在本项目下专业的名称
			 getDepartmentId:function(){
				uni.showLoading({
					title:"获取中",
					success:()=>{
						Query.findUserProjectDepartmentByUserIdAndProjectId(_this.userInfo.id,_this.projectId)
						.then(data=>{
							console.log(data)
							uni.hideLoading();
							console.log("查询到的departmentId",data.data.records[0].departmentId)
							_this.departmentId = data.data.records[0].departmentId;
							_this.getDepartmentName(data.data.records[0].departmentId);
						})
						.catch(error=>{ 
							console.log(error)
							uni.showToast({
								title:"网络错误",
								icon:"loading",
								duration:1000
							})
						})
					}
				})
			 },
			 
			 //根据专业的id去查询专业.departmentName
			getDepartmentName:function(departmentId){
					_this = this;
					uni.showLoading({
					title:"获取中",
					success:()=>{
						uni.request({
							url:departmentQuery,
							method:"POST",
							data:{
								id:departmentId
							},
							dataType:'json'
						})
						.then(data=>{
							uni.hideLoading();
							console.log(data)
							console.log("查询到的执行人的专业",data[1].data.data.records[0].departmentName);
							_this.departmentName = data[1].data.data.records[0].departmentName;
							_this.departmentNum = data[1].data.data.records[0].departmentNum;
						})
						.catch(Error=>{
							console.log(Error)
							uni.showToast({
								title:"网络错误",
								duration:1000,
								icon:'loading'
							})
						})
					}
				})
			},
			
			//根据项目的id去获取projectName
			findProjectNameByProjectId:function(){
				uni.showLoading({
					title:"获取中",
					success:()=>{
						uni.request({
						   url:projectQuery,
						   data:{
							   id:_this.projectId
						   },
						   method:"POST",
						   dataType:'json'
						})
						.then(data=>{
							console.log("查询到的项目信息",data)
							_this.projectName = data[1].data.data.records[0].projectName;
						})
						.catch(Error=>{
							uni.showToast({
								title:"网络错误",
								duration:500,
								icon:"loading"
							})
						})
					}
				})
			},
			
			
			//选择有限级的时候
			pickerPriorityChange:function(e){
				let index = parseInt(e.detail.value);
				this.taskPriority = this.priority[index];
			},
			
		    //首先获取在本项目下本冲刺的本专业的情况下taskOrder 
			//根据查找到任务的总数进行统计,然后将统计的个数+1
			getNowHadTaskOrder:function(callback){
				_this = this;
				console.log("查询条件",{
							 // taskExcuteUserId:_this.userInfo.id,
							  taskSprint:_this.sprintId,
							  projectId:_this.projectId,
							  departmentId:_this.departmentId,
							  pageNum:0,
							  pageSize:1000
							})
				uni.showLoading({
					title:"获取中",
					success:()=>{
						uni.request({
							url:taskQuery,
							method:"POST",
							data:{
							  taskSprint:_this.sprintId,
							  projectId:_this.projectId,
							  departmentId:_this.departmentId,
							  pageNum:0,
							  pageSize:1000
							},
							dataType:'json'
						})
						.then(data=>{
							
							//查到任务并且返回长度.
							uni.hideLoading();
							console.log("查询到的专业下的任务",data);
							if(data[1].data.data.records.length){
								let taskLength = data[1].data.data.records.length;
								callback(taskLength)
							}else{
							    callback(0);	
							}
						})
					    .catch(error=>{
							uni.hideLoading();
							uni.showToast({
								title:"网络错误",
								icon:"loading",
								duration:1000
							})
						})
					}
				})
			},
			
			// //输入首字母
			// //这里获取所有的该项目下该冲刺下该专业该用户的任务
			// //即调用getNowHadTaskOrder函数来计算总数然后将输入的值
			// //和总数＋1进行拼接作为最终的taskOrder赋给this.taskOrder	
			// inputTaskOrder:function(e){
			// 	let taskOrder = e.target.value;
			//     let reg = /[A-Z]/i;
			// 	if(reg.test(taskOrder)&&taskOrder.length===1){
			// 		this.getNowHadTaskOrder(length=>{    //0-9自动前缀补充0
			// 			let len = length.toString();
			// 			if(len.length===1&&len.length!=9){
			// 			  this.taskOrder = taskOrder+("0"+(length+1));	
			// 			}else{
			// 			  this.taskOrder = taskOrder+(length+1);	
			// 			}
			// 		})
			// 	}else{
			// 		this.taskOrder = "";
			// 		uni.showToast({
			// 			title:"输入有误",
			// 			icon:"none",
			// 			duration:500
			// 		})
			// 	}
			// },
			// 
			
			//输入任务的内容
			inputTaskName:function(e){
				if(e.detail.value.length<=30){
					this.taskName = e.detail.value
				}else{
					this.taskName = "";
					uni.showToast({
						icon:"none",
						title:"任务长度过长"
					})
				}
			},  
			
			//输入预估工时
			inputTaskPredictTime:function(e){
				let taskPredictTime = parseFloat(e.detail.value);
				this.taskPredictTime = taskPredictTime;
			},
			
			//查找冲刺的启动时间和截止时间
			querySprintInfo:function(callback){
				_this = this;
				uni.request({
					url:sprintQuery,
					method:"POST",
					data:{
						id:_this.sprintId,
					},
					dataType:'json'
				})
				.then(data=>{
					let sprint = data[1].data.data.records[0];
					let nowTime = formatDate(new Date());
					let startTime = sprint.startTime;
					let endTime = sprint.endTime;
					compareTime(startTime,endTime,nowTime,(jude)=>{
						if(jude){
							callback(true)
						}else{
							callback(false)
						}
					})
				})
				.catch(Error=>{
					uni.showToast({
						title:'网络错误',
						icon:"none",
						duration:1000
					})
				})
			},
			
			
		    //添加任务提交的函数
			submitAddTask:function(e){
			   _this = this;
			   addFormId(_this.userInfo.openId,e.detail.formId);
			   
			   let taskOrder;
			   
			   _this.getNowHadTaskOrder(length=>{       //0-9自动前缀补充0
			   	let len = length.toString();
			   	if(len.length===1&&len.length!=9){
			   	  taskOrder = _this.departmentNum+"0"+(length+1);
				  _this.taskOrder = taskOrder;
				  console.log(taskOrder)
				  _this.addTask();
			   	}else{
			   	    taskOrder = _this.departmentNum+(length+1);	
				   _this.taskOrder = taskOrder
				   _this.addTask();
			   	 }
			   })
			},
			
			//addTask
			addTask:function(){
				 //信息不为空的时候进行提交
				let data = {
					   "departmentId": _this.departmentId,
					   "isReview": 0,
					   "projectId": _this.projectId,
					   "sprintId": _this.sprintId,
					   "taskChargeUserId": _this.taskChargeUserId,
					   "taskExecuteUserId": _this.taskExcuteUserId,
					   "taskName": _this.taskName,
					   "taskOrder": _this.taskOrder,
					   "taskPlainId":"",                
					   "taskPredictTime": _this.taskPredictTime,
					   "taskPriority": _this.taskPriority,
					   "taskState": 0,
				};
				console.log("提交的任务的信息为",data)
				if(_this.projectId&&_this.taskExcuteUserId
				    &&_this.taskChargeUserId&&_this.sprintId
				    &&_this.taskChargeUserName&&_this.taskPriority
				    &&_this.taskPredictTime&&_this.taskOrder
				    &&_this.taskName&&_this.departmentName
				   ){
						 _this.querySprintInfo(jude=>{
							 if(jude){
									 uni.showLoading({
										title: '提交中',
										mask: false,
										success:()=>{
											uni.request({
											url:taskAdd,
											method:'POST',
											data:{
												  "departmentId": _this.departmentId,
												  "isReview": 0,
												  "projectId": _this.projectId,
												  "sprintId": _this.sprintId,
												  "taskChargeUserId": _this.taskChargeUserId,
												  "taskExecuteUserId": _this.taskExcuteUserId,
												  "taskName": _this.taskName,
												  "taskOrder": _this.taskOrder,
												  "taskPlainId":"",                
												  "taskPredictTime": _this.taskPredictTime,
												  "taskPriority": _this.taskPriority,
												  "taskState": 0,
												  "lastChangeStr":_this.taskPredictTime
											},
											dataType:'json'
										})
										.then(data=>{
											uni.hideLoading();
											console.log("提交任务成功",data);
											_this.pushAddMessagePush();
										})
										.catch(Error=>{
										  uni.showToast({
											 title: '网络错误',
											 duration:500,
											 icon:"none"
										  });
										})	
									} 
								 });
							 }else{
								 uni.showToast({
									title:"冲刺已完成无法添加",
									duration:1000,
									icon:"none"
								 })
							  }
							})
						 }else{
							 uni.showToast({
								title:"请完善信息",
								duration:1000,
								icon:"none"
							 })
						 }
			},
			
			//添加任务增加微信消息推送
			pushAddMessagePush:function(){
				_this = this;
				console.log("项目负责人的openId",_this.openId)
				let applyTime = formatDate(new Date());
				uni.showLoading({
					title:"提交提交中", 
					success:()=>{
						uni.request({
							url:messageSend,
							method:"POST",
							data:{
								"touser": _this.openId,                      //目标用户
							    "template_id": "Paifa1aKUvziS35PwSX4L8XWX-aVMamivXDW5c9XOzc",    //模板id
								"page": "pages/reviewTask/reviewTask",
							    "form_id": "",                                 //后台寻找这个目标用户的formId
								"data": {
									 "keyword1": {
										"value": _this.userInfo.trueName,
									},
									"keyword2": { 
										"value": _this.projectName
									},
									"keyword3": {
										"value": _this.departmentName
									},
									"keyword4": {
										"value":"新增"+_this.taskName
									},
									"keyword5": {
										"value":applyTime
									},
									"emphasis_keyword": "keyword1.DATA"
								}
							},	
						}) 
						.then(data=>{
							uni.showToast({
								title:"提交成功等待审核",
								icon:"../../static/img/Icon/success.png",
								duration:500,
								success:()=>{
									uni.navigateBack({
										delta:1
									})
								}
							})
							console.log("消息推送成功",data)
						})
						.catch(Error=>{
							uni.showToast({
								title:"网络错误",
								icon:"loading",
								duration:500
							})
						})
					}
				})
			}
				  
		 }
	}
